    flex-wrap: wrap;
    .pic-404 {
      width: 100%;
      img {
        width: 100%;
      }
    }
    .bullshit {
      width: 100%;
      margin: 0;
      text-align: center;
    }
    .bullshit__return-home {
      margin: 0 auto;
    }
  }
